@import "./../../styles/responsive";

.container {
  background: #f5f5fc;
  justify-content: flex-start;
  background-image: url(../../assets/images/contactBg.png);
  background-repeat: no-repeat;
  background-position: right top;

  & > div:first-child {
    padding: 0;
    padding-top: 5.8%;
  }

  & > div:last-child {
    align-items: flex-start;
  }

  h1 {
    color: white;
    line-height: 7rem;
  }

  .right {
    align-items: flex-start;
    justify-content: flex-start;
    width: 70%;
    
  }

  .left {
    background: #3b3b4c;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.16);
    height: 100%;
    width: 100%;
    padding: 5%;
    color: #a6a6c3;

    .sub1 {
      line-height: 3rem;
      font-size: 1.25rem;
      padding-right: 20%;
      margin-bottom: 10%;
    }
  }

  h3 {
    line-height: 5rem;
    color: #e2e2e8;
    display: flex;
    flex: 1;

    img {
      margin: 1rem;
    }
  }

  @include mobile {
    & > div:first-child {
      padding-top: 25%;
    } 

    .left {
      .sub1 {
        padding-right: 1%;
      }
    }

    .right {
      width: 100%;
    }

    h3 {
      line-height: 3rem;
    }
  
  }

  @include mobileLandscape { 
    padding-top: 6%;
    .select { 
      width: 80%;
    }
  }
}
